.mouse-tracker {
  --size-init: 0.6rem;
  --size-active: 1.5rem;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: var(--size-init);
  height: var(--size-init);
  background: rgba(255, 255, 255, 0.2);
  opacity: 0.8;
  transform: translate(-50%, -50%) translate(var(--mouse-x), var(--mouse-y));
  transition: border-radius 0.15s ease-in-out, background-color 0.1s ease-in-out, width 0.1s ease-in-out, height 0.1s ease-in-out;
  pointer-events: none; }
  .mouse-tracker::before, .mouse-tracker::after {
    content: '';
    position: absolute;
    border-width: 1px;
    border-style: solid;
    border-color: var(--theme-contrast); }
  .mouse-tracker::before {
    width: 90%;
    height: 90%;
    z-index: -2;
    border-bottom: none;
    border-right: none;
    background-color: transparent; }
  .mouse-tracker::after {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: var(--mouse-bg); }
  .mouse-tracker.on-link {
    width: var(--size-active);
    height: var(--size-active);
    cursor: pointer;
    background-image: linear-gradient(460deg, var(--color-a), var(--color-a));
